body {
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 375px;
  margin: 0 auto;
  font-family: "Golos", "Open Sans", sans-serif;
  background-color: #4f73cd;
  color: #ffffff;
  overflow-x: hidden; }

img {
  max-width: 100%;
  height: auto; }

body,
*,
*::before,
*::after {
  box-sizing: border-box; }

@font-face {
  font-family: "Golos";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("Golos Regular"), local("Golos-Regular"), url("../fonts/golos-regular.woff2") format("woff2"), url("../fonts/golos-regular.woff") format("woff"); }

@font-face {
  font-family: "Golos";
  font-display: swap;
  font-weight: 500;
  font-style: normal;
  src: local("Golos Medium"), local("Golos-Medium"), url("../fonts/golos-medium.woff2") format("woff2"), url("../fonts/golos-medium.woff") format("woff"); }

@font-face {
  font-family: "Golos";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: local("Golos Bold"), local("Golos-Bold"), url("../fonts/golos-bold.woff2") format("woff2"), url("../fonts/golos-bold.woff") format("woff"); }

.container {
  width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
  min-width: 0;
  padding-bottom: 134px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (max-width: 1440px) {
    .container {
      width: 768px;
      margin: 0 auto;
      padding: 0 40px;
      padding-bottom: 52px; } }
  @media (max-width: 768px) {
    .container {
      width: 375px;
      margin: 0 auto;
      padding: 0 21px 0 20px;
      padding-bottom: 40px; } }

.slider {
  margin: 44px auto 0;
  width: 1170px;
  position: relative;
  list-style: none;
  padding: 0;
  z-index: 1; }
  @media (max-width: 1440px) {
    .slider {
      width: 100%; } }

.slider__title {
  margin: 0;
  padding: 0;
  font-size: 36px;
  line-height: 53px;
  font-weight: 700;
  margin-bottom: 17px; }

.slider__filter {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 32px;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }
  @media (max-width: 1440px) {
    .slider__filter {
      width: 100%; } }
  @media (max-width: 768px) {
    .slider__filter {
      flex-direction: column;
      align-items: stretch;
      background-color: #254595;
      padding-top: 11px;
      border-radius: 0 0 8px 8px; } }

.slider__radio {
  margin-right: 10px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .slider__radio:last-of-type {
    margin-right: 0; }
  @media (max-width: 768px) {
    .slider__radio {
      margin: 0 0 6px 0; }
      .slider__radio:last-of-type {
        margin-bottom: 21px; } }

.slider__radio input {
  margin: 0;
  padding: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }
  .slider__radio input:checked + label {
    background-color: rgba(255, 255, 255, 0.3);
    transition: 0.2s ease-in-out; }
    @media (max-width: 768px) {
      .slider__radio input:checked + label {
        background-color: #3259b9; }
        .slider__radio input:checked + label::before {
          display: block; } }

.slider__radio label {
  padding: 8px 20px;
  border-radius: 8px; }
  .slider__radio label:hover {
    background-color: rgba(255, 255, 255, 0.5);
    transition: 0.2s ease-in-out; }
  @media (max-width: 1440px) {
    .slider__radio label {
      font-size: 16px;
      line-height: 20px;
      font-weight: 500; } }
  @media (max-width: 768px) {
    .slider__radio label {
      font-size: 16px;
      line-height: 24px;
      font-weight: 500;
      position: relative;
      padding: 6px 30px 9px 40px;
      width: 100%;
      border-radius: 0;
      background-color: #254595; }
      .slider__radio label::before {
        position: absolute;
        content: "";
        display: none;
        width: 10px;
        height: 6px;
        top: 13px;
        left: 20px;
        border-left: 2px solid #ffffff;
        border-bottom: 2px solid #ffffff;
        transform: rotate(-45deg); } }

.slider__wrapper {
  width: 1330px;
  margin: 0 auto 20px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.slider__outer {
  width: 1170px;
  margin: 0 auto;
  overflow: hidden;
  z-index: 30;
  position: relative;
  min-height: 530px; }
  @media (max-width: 1440px) {
    .slider__outer {
      width: 688px;
      margin: 0; } }
  @media (max-width: 768px) {
    .slider__outer {
      width: 334px; } }

.slider__inner {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 30px;
  position: absolute;
  top: 0;
  left: 0;
  width: 1770px;
  height: 530px;
  pointer-events: none; }
  @media (max-width: 1440px) {
    .slider__inner {
      grid-gap: 20px; } }
  @media (max-width: 768px) {
    .slider__inner {
      grid-column-gap: 18px;
      grid-row-gap: 20px; } }

.card {
  position: relative;
  height: 250px;
  display: flex;
  align-items: stretch;
  border-radius: 8px;
  background-color: #254595;
  box-shadow: 0 0 20px 3px rgba(0, 0, 0, 0.25); }
  .card--hide {
    display: none;
    transition: 1s ease-in; }
  .card--reverse .card__description {
    left: 0; }
  @media (max-width: 1440px) {
    .card {
      height: 317px; } }
  @media (max-width: 768px) {
    .card {
      height: 230px;
      flex-direction: column; } }

.card:hover {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  transition: 0.2s ease-in-out; }
  .card:hover .card__cover {
    z-index: 10; }
  .card:hover img {
    border-radius: 8px 0 0 8px; }
  .card:hover .card__description {
    left: 100%;
    z-index: 10;
    visibility: visible;
    border-radius: 0 8px 8px 0; }

.card--reverse:hover {
  border-radius: 0 8px 8px 0; }
  .card--reverse:hover img {
    border-radius: 0 8px 8px 0; }
  .card--reverse:hover .card__description {
    left: -400px;
    border-radius: 8px 0 0 8px; }

.card__cover {
  position: relative;
  cursor: pointer; }
  @media (max-width: 1440px) {
    .card__cover {
      height: 317px; } }
  @media (max-width: 768px) {
    .card__cover {
      height: 230px; } }

.card img {
  min-width: 170px;
  height: 250px;
  border-radius: 8px; }
  @media (max-width: 1440px) {
    .card img {
      min-width: 216px;
      height: 317px; } }
  @media (max-width: 768px) {
    .card img {
      min-width: 158px;
      height: 230px; } }

.card__name {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  padding: 12px 17px;
  max-width: 170px; }

.card__city {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  margin-bottom: 2px; }
  @media (max-width: 1440px) {
    .card__city {
      margin-bottom: 5px; } }

.card__country {
  margin: 0;
  padding: 0; }

.card__description {
  position: absolute;
  top: 0;
  right: 0;
  visibility: hidden;
  width: 400px;
  height: 250px;
  padding: 25px 26px 22px 30px;
  background-color: #254595;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  @media (max-width: 1440px) {
    .card__description {
      height: 317px;
      width: 473px;
      padding: 36px 26px 27px 21px; } }
  @media (max-width: 768px) {
    .card__description {
      height: 230px; } }

.card__close {
  display: none;
  position: absolute;
  width: 17px;
  height: 17px;
  top: 15px;
  right: 15px;
  align-self: flex-end;
  background-color: transparent;
  border: none; }
  .card__close::before, .card__close::after {
    position: absolute;
    content: "";
    width: 2px;
    height: 18px;
    top: 0;
    right: 7px;
    background-color: #ffffff;
    transform: rotate(45deg); }
  .card__close::after {
    transform: rotate(135deg); }
  @media (max-width: 1440px) {
    .card__close {
      display: block; } }

.card__text {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 22.4px;
  font-weight: 400; }

.card__info {
  font-size: 16px;
  line-height: 24px;
  font-weight: 700px;
  padding: 13px 21px;
  border: 2px solid #ffffff;
  font-family: "Golos", "Open Sans", sans-serif;
  color: #ffffff;
  background-color: transparent;
  border-radius: 8px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  padding-top: 10px;
  align-self: flex-start; }
  .card__info:hover {
    border-color: #0047fe;
    background-color: #0047fe;
    transition: 0.2s ease-in-out; }

.arrows {
  font-size: 16px;
  line-height: 24px;
  font-weight: 700px;
  padding: 13px 21px;
  border: 2px solid #ffffff;
  font-family: "Golos", "Open Sans", sans-serif;
  color: #ffffff;
  background-color: transparent;
  border-radius: 8px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 0;
  border: none; }
  .arrows:disabled::after {
    opacity: 0.2;
    cursor: not-allowed; }
  @media (max-width: 1440px) {
    .arrows {
      display: none; } }

.arrows--left::before,
.arrows--right::before {
  position: absolute;
  content: "";
  content: "";
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%; }

.arrows--left::after,
.arrows--right::after {
  position: absolute;
  content: "";
  content: "";
  color: #ffffff;
  width: 40px;
  height: 40px;
  top: 0;
  left: -2px;
  background-image: url("../img/icon-left.svg");
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: center; }

.arrows--left:hover::before,
.arrows--right:hover::before {
  background-color: #0047fe;
  transition: 0.2s ease-in-out; }

.arrows--right::after {
  background-image: url("../img/icon-right.svg");
  left: 2px; }

.pagination {
  display: none; }
  @media (max-width: 1440px) {
    .pagination {
      display: block; } }
